                                            <v-col>
                                                <v-flex>
                                                    <v-menu
                                                        v-model="menuDate2"
                                                        :close-on-content-click="false"
                                                        :nudge-right="40"
                                                        transition="scale-transition"
                                                        offset-y																
                                                    >
                                                        <template v-slot:activator="{ on }">
                                                            <v-text-field
                                                                v-model="fechaincorporacion"
                                                                label="Fecha inicio"
                                                                readonly
                                                                v-on="on"                                                
                                                            ></v-text-field>
                                                        </template>
                                                        <v-date-picker 
