                                                </v-flex>
                                            </v-col>

                                            <v-col>    
                                                <v-flex>
                                                    <v-menu
                                                        v-model="menuDate1"
                                                        :close-on-content-click="false"
                                                        :nudge-right="40"
                                                        transition="scale-transition"
                                                        offset-y																
                                                    >
                                                        <template v-slot:activator="{ on }">
                                                            <v-text-field
                                                                v-model="fechaterejecucion"
                                                                label="Fecha terminación"
                                                                readonly
                                                                v-on="on"                                                																	
                                                            ></v-text-field>
                                                        </template>
                                                        <v-date-picker 
                                                            v-model="fechaterejecucion" @input="menuDate1 = false" :type="'date'"
                                                        >
                                                        </v-date-picker>
                                                    </v-menu>
                                                </v-flex>
                                            </v-col>
                                            <v-col>                                                        
                                                <v-flex>										
                                                    <v-text-field v-model="tiempoejecucion" type="number" label="Tiempo de ejecución (Dias)"  
                                                                    readonly>
                                                    </v-text-field>															
                                                </v-flex> 
                                            </v-col>
                                        </v-row>

                                        <v-row>
                                            <v-col>
                                                <v-flex>										
                                                    <v-text-field v-model="cofinanciado" 
                                                        label="Cofinanciado"                                                     
                                                        :messages="calculaTasa(cofinanciado)"
                                                        :prefix="simbolomoneda"
                                                        @blur="formatearValor(cofinanciado,'cofinanciado')"
                                                    >
                                                    </v-text-field>
                                                    
                                                </v-flex>
                                            </v-col>
                                            <v-col>    
                                                <v-flex>										
                                                    <v-text-field v-model="contrapartida"
                                                        label="Contrapartida"                                                     
                                                        :messages="calculaTasa(contrapartida)"
                                                        :prefix="simbolomoneda"
                                                        @blur="formatearValor(contrapartida,'contrapartida')"
                                                    >
                                                    </v-text-field>
                                                
                                                </v-flex>
                                            </v-col>

                                            <v-col>
                                                <v-flex>										
                                                    <v-text-field v-model="totalizar" 
                                                        label="Valor total"                                                      
                                                        :readonly="true"
                                                        :messages="calculaTasa(totalizar)"
                                                        :prefix="simbolomoneda"                                                                                                
                                                    >
                                                    </v-text-field>                                   
                                                </v-flex>
                                            </v-col>

                                            <v-col>
                                                <v-flex>										
                                                    <v-text-field v-model="totaldinero" 
                                                        label="Total en dinero"                                                     
                                                        :messages="calculaTasa(totaldinero)"
                                                        :prefix="simbolomoneda"
                                                        @blur="formatearValor(totaldinero,'totaldinero')"
                                                    >
                                                    </v-text-field>                                   
                                                </v-flex>
                                            </v-col>

                                            <v-col>    
                                                <v-flex>										
                                                    <v-text-field v-model="totalespecie" 
                                                        label="Total en especie"                                                    
                                                        :messages="calculaTasa(totalespecie)"
                                                        :prefix="simbolomoneda"
                                                        @blur="formatearValor(totalespecie,'totalespecie')"
                                                    >
                                                    </v-text-field>                                   
                                                </v-flex>  
                                            </v-col>
                                        </v-row>    
                                    </v-form>									
                                </v-layout>
