                        </v-card-text>
                    </v-card>

                    <v-data-table class="text-no-wrap" :loading="loading" :headers="headers" :items="data" :items-per-page="5">
                        
                        <template v-slot:item.cofinanciado="{ item }">
							<span>{{ item.cofinanciado |  currency({
									symbol: item.moneda,
									thousandsSeparator: '.',
