									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>

                        <template v-slot:item.contrapartida="{ item }">
							<span>{{ item.contrapartida |  currency({
									symbol: item.moneda,
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>	

                        <template v-slot:item.totaldinero="{ item }">
							<span>{{ item.totaldinero |  currency({
									symbol: item.moneda,
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>

                        <template v-slot:item.totalespecie="{ item }">
							<span>{{ item.totalespecie |  currency({
									symbol: item.moneda,
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>

                        <template v-slot:item.valortotal="{ item }">
							<span>{{ item.valortotal |  currency({
									symbol: item.moneda,
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>	    

                        <template v-slot:item.action="{ item }">							
                            <v-icon
								small
								class="mr-5"
								color="primary" 
								:disabled="loading"
                                v-show="!readonly"
								@click="dialog = true, edit=1, itemactual=item"
							>
								edit
							</v-icon>

                            <v-icon
                                small
                                class="mr-5"
                                color="primary" 
                                :disabled="loading"
                                :loading="loading"
                                v-show="!readonly"
                                @click="dialog_del=true,itemactual=item"                                
                            >
                                delete
                            </v-icon>						
                                                
                        </template>
                    </v-data-table>
                </v-card-text>

            </v-card> 
        </v-flex>

        <!-- <v-dialog v-model="dialog" max-width="1400px"> -->
        <!-- </v-dialog> -->

        <v-dialog v-model="dialog_del" width="550">

            <v-card>

                <v-card-title>
                    <span class="headline red--text">Eliminar Otrosí</span>
                </v-card-title>

                <v-card-text>					
                    ¿Esta seguro de eliminar el Otrosí seleccionado?.					
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" dark text @click="dialog_del=false">Cancelar</v-btn>
                    <v-btn color="red" text @click="deleteItem" >Eliminar</v-btn>
                </v-card-actions>

            </v-card>

        </v-dialog>
