</template>

<script>
import conveniosotrosiService from "../servicios/ConveniosOtrosiService"
import conveniosService from "../servicios/ConveniosService"

export default {
    name: 'OtroSi',
    props: ['convenio'],
    data (){

        return {
            loading: false,
            dialog:false,
            edit:-1,
            dialog_del:false,				
            processing:false,
            formTitle:'',
            itemactual:'',          
            valid:false,    
            fechasuscripcion:'',
            fechaincorporacion:'',
            fechaterejecucion:'',    
            tiempo:0,
            cofinanciado:0,
            contrapartida:0,
            totaldinero:0,
            totalespecie:0,

            tasa:'',
            convenioproyecto:'',
			nombreproyecto:'',
			codigocoordinador:'',

            menuDate:false,
            menuDate1:false,
            menuDate2:false,

            simbolomoneda:'',
            
            data:[],

            readonly:false,

            headers: [
                { text: 'Fecha inicio', value: 'fechasuscripcion',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },                 
                { text: 'Fecha incorporación', align: 'left', sortable: false, value: 'fechaincorporacion', class:'subtitle-1 font-weight-bold black--text'},
                { text: 'Fecha terminación de la ejecución', value: 'fechaterejecucion',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Tiempo de ejecución (Dias)', value: 'tiempo',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Coofinanciado', value: 'cofinanciado',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Contrapartida', value: 'contrapartida',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Valor total', value: 'valortotal',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Total en dinero', value: 'totaldinero',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Total en especie', value: 'totalespecie',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' },
                { text: 'Acción', value: 'action',sortable: false,align: 'left', class:'subtitle-1 font-weight-bold black--text' }          
            ],

            rules: {
                required: value => !!value || 'Campo requerido.',
                valor: value => value>0 || 'El valor debe ser mayor a 0.',
                minDate: value => value < new Date().toISOString() || 'La fecha no puede ser superior al dia de hoy',
                maxDate: value => value >= new Date().toISOString() || 'La fecha no puede ser inferior al dia de hoy'            
            }	

        }
    },

    methods: {

        showMessage (snack, time, color, text) {

           this.$root.$emit('showSnack', {snack: snack, time: time, color: color, text: text})

        },
        
        envioNotificaciones(mensajenotificacion,asuntocorreo, mensajecorreo){
            
            this.$emit('envioNotificaciones', {mensajenotificacion: mensajenotificacion,asuntocorreo: asuntocorreo, mensajecorreo: mensajecorreo})

        },            

        resetForm () {

            this.codigo='0'           
            this.fechasuscripcion=''
            this.fechaterejecucion=''
            this.fechaincorporacion=''           
            this.valortotal=0            
            this.fuentefinanciacion=''
            this.cofinanciado=0
            this.contrapartida=0
            this.totaldinero=0
            this.totalespecie=0                    	
            this.tiempo='0'
        },
        
        cerrarDialogo(){

            this.resetForm()
            this.dialog = false
            this.edit=-1
        },

        calculaTasa(valor){             

            var nuevoVal = this.limpiarMoneda(valor)
            var tasaVal = this.tasa
            var total = 0
            var valFormato = ''

            if(tasaVal == 0)
                total = nuevoVal					
            else
                total = (nuevoVal * tasaVal)

            valFormato = '$ ' + this.number_format(total,2)

            return valFormato

        },

        getTasa(){

            this.tasa = this.codigomoneda.tasa
            this.simbolomoneda = this.codigomoneda.simbolo

        },
        
        traerDataConvenio(){

            var _this = this
           
            _this.convenioproyecto = ''
            _this.nombreproyecto = ''
            _this.codigocoordinador = ''
            _this.tasa = 0

            conveniosService.getDataXId(this.convenio)
                .then(function(res) {

                    if (res.request) {
                                    
                        if(res.data){

                            _this.convenioproyecto = res.data.data[0].numero
                            _this.nombreproyecto = res.data.data[0].nombre
                            _this.codigocoordinador = res.data.data[0].codigocoordinador
                            _this.tasa = res.data.data[0].tasa
                            
                        }

                    }
                    
                    _this.loading = false                
                   
                    
            }).catch(function(err) {

                    
            })                

        },

        traerdata(convenio){

            var _this = this
            this.loading = true

            conveniosotrosiService.getDataXProyecto(convenio)
                .then(function(res) {

                    if (res.request) {
                                    
                        if(res.data){

                            _this.data = res.data.data 

                        }  else {
                            
                            _this.data = []
                        
                        }

                    } else {                        	

                        _this.data = []    
                                                           	
                    }    
                    _this.loading = false                
                    
            }).catch(function(err) {

                    _this.data = []

                    _this.loading = false                
                
            })

        },

        deleteItem(){
            
            var _this = this
            this.loading = true            

            conveniosotrosiService.DelData(_this.itemactual.codigo, _this.convenio)
                .then(function(res) {

                    if(res.statusText){

                        _this.showMessage(true,3000,'success','Se eliminó el Otro Si con éxito')

                        _this.del_item = ''	              	  		
                        _this.traerdata(_this.convenio)
                        _this.dialog_del = false
                        
                        _this.agregaBitacora('ELIMINACION','SE ELIMINÓ EL OTRO SI CON FECHA DE INICIO:' + _this.itemactual.fechasuscripcion + 
                                                ', FECHA DE INCORPORACION:' + _this.itemactual.fechaincorporacion + 
                                                ', FECHA TERMINACION EJECUCION: ' +  _this.itemactual.fechaterejecucion + 
                                                ', VALOR TOTAL: ' + _this.itemactual.valortotal +
                                                '. QUE PERTENECE AL CONVENIO: ' + _this.convenioproyecto + ' Y NOMBRE: ' + 
                                                _this.nombreproyecto)

                        var mensajecorreo = "Cordial Saludo,<br><br> " +
                                            "Se eliminó el Otro Si con Fecha de Inicio : '" + _this.itemactual.fechasuscripcion + 
                                            "', Fecha de Incoprporación: '" + _this.itemactual.fechaincorporacion +
                                            "', Fecha de Terminación Ejecucion: '" + _this.itemactual.fechaterejecucion +
                                            "', Valor Total: '" + _this.itemactual.valortotal + 
                                            "' del # de Convenio " + 
                                            _this.convenioproyecto + ", Nombre: " + _this.nombreproyecto +
                                            "<br><br>Agradeciendo su atención,<br><br> " +                        
                                            "<b>SINCHI</b><br><br> " +
                                            "<i>Este es un servicio de notificaciones generado automáticamente por medio de " +
                                            "correo electrónico, no lo responda.</i> <br>"
                        
                        var mensajenotifica = "Se eliminó el Otro Si con Fecha de Inicio : '" + _this.itemactual.fechasuscripcion + 
                                            "', Fecha de Incoprporación: '" + _this.itemactual.fechaincorporacion +
                                            "', Fecha de Terminación Ejecucion: '" + _this.itemactual.fechaterejecucion +
                                            "', Valor Total: '" + _this.itemactual.valortotal +  
                                            "' del # de Convenio " + 
                                            _this.convenioproyecto + ", Nombre: " + _this.nombreproyecto

                        _this.envioNotificaciones(mensajenotifica,'Eliminación de otro si de Convenio',mensajecorreo)                                 

                    } else {	                                         	

                        _this.showMessage(true,3000,'error',res.response.data.message) 	                  	
                    } 	                   
                    _this.del_item = ''						
                    _this.loading = false	                 
                    
            }).catch(function(err) {

                _this.showMessage(true,3000,'error',err)	    
                _this.loading = false          	
                
            })

        },        

        validaSave(){

            if(this.itemactual.fechasuscripcion == this.fechasuscripcion && this.itemactual.fechaterejecucion == this.fechaterejecucion &&
                this.itemactual.fechaincorporacion == this.fechaincorporacion && this.itemactual.cofinanciado == this.cofinanciado && 
                this.itemactual.contrapartida == this.contrapartida && this.itemactual.totaldinero == this.totaldinero && 
                this.itemactual.totalespecie == this.totalespecie){

                return false

            } else {

                return true

            }	
        },
            
        saveitem(){            
            
            this.loading = true           
            var _this = this

            var datbit = ''

            var data = {codigo:this.codigo,convenio:this.convenio,fechasuscripcion:this.fechasuscripcion, fechaterejecucion:this.fechaterejecucion,
                        fechaincorporacion:this.fechaincorporacion,valortotal: this.limpiarMoneda(this.totalizar),
                        cofinanciado:this.limpiarMoneda(this.cofinanciado),contrapartida:this.limpiarMoneda(this.contrapartida),
                        totaldinero:this.limpiarMoneda(this.totaldinero),totalespecie:this.limpiarMoneda(this.totalespecie),tiempo:this.tiempoejecucion}

            let ValidaFechas = this.validaFechas()
            if(ValidaFechas != ''){

                this.showMessage(true,5000,'error',ValidaFechas)
                this.loading = false
                return
            }

            let validarcontrpartida = this.validarTotalContrapartida()
            if(validarcontrpartida != ''){

                this.showMessage(true,5000,'error',validarcontrpartida)
                this.loading = false
                return
            }
                
            if(this.edit){

                var valida = this.validaSave()

                if(!valida){

                    this.showMessage(true,3000,'error','No hay datos para actualizar')
                    this.loading = false
                    return;
                }

                datbit = ''

                if (this.itemactual.fechasuscripcion != this.fechasuscripcion)
                    datbit += 'Fecha Inicio: ' + this.itemactual.fechasuscripcion + ' a ' + this.fechasuscripcion					

                if (this.itemactual.fechaincorporacion != this.fechaincorporacion) {
                    datbit += datbit == '' ? 'Fecha de Incorporación: ' + this.itemactual.fechaincorporacion + ' a ' + this.fechaincorporacion : 
                                ', Fecha de Incorporación: ' + this.itemactual.fechaincorporacion + ' a ' + this.fechaincorporacion 
                }					
                
                if (this.itemactual.fechaterejecucion != this.fechaterejecucion) {
                    datbit += datbit == '' ? 'Fecha Terminación Ejecución: ' + this.itemactual.fechaterejecucion + ' a ' + this.fechaterejecucion : 
                                ', Fecha Terminación Ejecución: ' + this.itemactual.fechaterejecucion + ' a ' + this.fechaterejecucion 
                }

                if (this.itemactual.cofinanciado != this.cofinanciado) {
                    datbit += datbit == '' ? 'Valor Cofinanciado: ' + this.itemactual.cofinanciado + ' a ' + this.cofinanciado : 
                                ', Valor Cofinanciado: ' + this.itemactual.cofinanciado + ' a ' + this.cofinanciado 
                }

                if (this.itemactual.contrapartida != this.contrapartida) {
                    datbit += datbit == '' ? 'Valor Contrapartida: ' + this.itemactual.contrapartida + ' a ' + this.contrapartida : 
                                ', Valor Contrapartida: ' + this.itemactual.contrapartida + ' a ' + this.contrapartida 
                }

                if (this.itemactual.contrapartida != this.contrapartida) {
                    datbit += datbit == '' ? 'Total en Dinero: ' + this.itemactual.totaldinero + ' a ' + this.totaldinero : 
                                ', Total en Dinero: ' + this.itemactual.totaldinero + ' a ' + this.totaldinero 
                }

                if (this.itemactual.contrapartida != this.contrapartida) {
                    datbit += datbit == '' ? 'Total en Especies: ' + this.itemactual.totalespecie + ' a ' + this.totalespecie : 
                                ', Total en Especies: ' + this.itemactual.totalespecie + ' a ' + this.totalespecie 
                }

                datbit = 'CODIGO: ' + this.codigo + ' (' + datbit + ')'

            } else {

                datbit += 'Fecha Inicio : ' + this.fechasuscripcion + ', Fecha de Incorporación: ' + 
                          this.fechaincorporacion + ', Fecha Terminación Ejecución: ' + this.fechaterejecucion + ', Valor Cofinanciado: ' + 
                          this.cofinanciado + ', Valor Contrapartida: ' + this.contrapartida + ', Total en Dinero: ' + this.totaldinero + 
                          ', Total en Especies: ' + this.totalespecie
            }
           
            conveniosotrosiService.guardarData(data)
	              .then(function(res) {

	              	  if (res.response) {                           
                            
                          var error = res.response.data.message
                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = ''
							var accbitacora = ''
                            
                            if(_this.edit){
								mensaje = 'Actualizó'
								accbitacora = 'ACTUALIZACION'
							} else {
								mensaje = 'Agregó'
								accbitacora = 'CREACION'
                            }
                                
	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' el Otro Si con éxito')             	  						
							
                            _this.agregaBitacora(accbitacora,datbit) 
                            
                            var mensajecorreo = "Cordial Saludo,<br><br> " +
                                            "Se " + mensaje + " el Otro Si para el # de Convenio " + 
                                            _this.convenioproyecto + ", Nombre: " + _this.nombreproyecto +
                                            "<br><br>Agradeciendo su atención,<br><br> " +                        
                                            "<b>SINCHI</b><br><br> " +
                                            "<i>Este es un servicio de notificaciones generado automáticamente por medio de " +
                                            "correo electrónico, no lo responda.</i> <br>"
                        
                            var mensajenotifica = "Se " + mensaje + " el Otro Si para el # de Convenio " + 
                                                _this.convenioproyecto + ", Nombre: " + _this.nombreproyecto

                            _this.envioNotificaciones(mensajenotifica,'Se ' + mensaje + ' el Otro Si del Convenio',mensajecorreo)

                            _this.traerdata(_this.convenio)
                            _this.dialog = false

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  }

	                    _this.loading = false
	                   	
	          	}).catch(function(err) {

	          		_this.showMessage(true,3000,'error',err)	    
	          		_this.loading = false          	
	          		
				})

        },

        number_format(amount, decimals) {

            amount += ''; // por si pasan un numero en vez de un string
            amount = parseFloat(amount.replace(/[^0-9\.]/g, '')) // elimino cualquier cosa que no sea numero o punto

            decimals = decimals || 0 // por si la variable no fue fue pasada

            // si no es un numero o es igual a cero retorno el mismo cero
            if (isNaN(amount) || amount === 0) 
                return parseFloat(0).toFixed(decimals)

            // si es mayor o menor que cero retorno el valor formateado como numero
            amount = '' + amount.toFixed(decimals)

            var amount_parts = amount.split('.'),
                regexp = /(\d+)(\d{3})/

            while (regexp.test(amount_parts[0]))
                amount_parts[0] = amount_parts[0].replace(regexp, '$1' + '.' + '$2')

            return amount_parts.join(',')
        },
        
        formatearValor(valor,NomVariable){

            let valoractual = valor
            valoractual = this.limpiarMoneda(valoractual)

            valoractual = this.number_format(valoractual,2)

            switch (NomVariable) {
                case 'cofinanciado':
                    
                    this.cofinanciado = valoractual
                    break

                case 'contrapartida':
                    
                    this.contrapartida = valoractual
                    break

                case 'totaldinero':
                    
                    this.totaldinero = valoractual
                    break
                
                case 'totalespecie':
                    
                    this.totalespecie = valoractual
                    break

                default:
                    break
            }
            
        },

        limpiarMoneda(val){

            var nuevoval = val.toString().replace(/[$.]/gi,'')
            nuevoval = nuevoval.replace(/[,]/gi,'.')

            return nuevoval.trim()				
        },

        /******************************** VALIDACIONES ***************************************
        **************************************************************************************/

        validaFechas(){				

            if(this.fechasuscripcion == '' || this.fechaincorporacion == '' || this.fechaterejecucion == '')
                return ''

            let FecSuscripcion = new Date(this.fechasuscripcion)
            let FecIncorporacion = new Date(this.fechaincorporacion)
            let FecTerminacion = new Date(this.fechaterejecucion)

            if(FecTerminacion <= FecSuscripcion)
                return 'La Fecha de Terminación debe ser superior a la Fecha de Inicio'

            if(FecTerminacion <= FecIncorporacion)	
                return 'La Fecha de Terminación debe ser superior a la Fecha de Incorporación'

            return ''	


        },

        validarTotalContrapartida(){

            let totalcontrapartida = parseFloat(this.limpiarMoneda(this.contrapartida))
            let totdinero = parseFloat(this.limpiarMoneda(this.totaldinero))
            let totespecie = parseFloat(this.limpiarMoneda(this.totalespecie))
            let totalbase = totdinero + totespecie

            if(totalbase > totalcontrapartida){
                return 'El Total en Especie y en Dinero, no puede superar el valor de la Contrapartida'
            }

            return ''
        },
                
        agregaBitacora(accion, detalle){              

            var data = {modulo:'OTRO SI ACUERDOS ESPECÍFICOS',accion:accion,detalle:detalle}
            
            this.$root.$emit('agregaBitacora', data)

        },

    },

    watch: {

        edit: function (val) {

            if(val=='1' || val=='2')
            {

                this.formTitle = val=='1' ? 'Editar' : 'Detalle (Solo Consulta)'

                this.codigo = this.itemactual.codigo                
                this.fechasuscripcion = this.itemactual.fechasuscripcion
                this.fechaterejecucion = this.itemactual.fechaterejecucion
                this.fechaincorporacion = this.itemactual.fechaincorporacion               
                this.valortotal = this.number_format(this.itemactual.valortotal,2)               
                this.fuentefinanciacion = this.number_format(this.itemactual.fuentefinanciacion,2)
                this.cofinanciado = this.number_format(this.itemactual.cofinanciado,2)
                this.contrapartida = this.number_format(this.itemactual.contrapartida,2)
                this.totaldinero = this.number_format(this.itemactual.totaldinero,2)
                this.totalespecie = this.number_format(this.itemactual.totalespecie,2)
                this.tiempo = this.itemactual.tiempo               
                this.tasa = this.itemactual.tasa		
                this.simbolomoneda = this.itemactual.moneda	

            } else{

                this.resetForm()
                this.formTitle = 'Nuevo'                
            }			   
        },

        convenio: function (val) {

            if(val!=0){
                this.traerdata(val)
                this.traerDataConvenio()
            }
        },

    },
    
    computed: {

        totalizar: function () {
            
            let valcofinanciado = this.limpiarMoneda(this.cofinanciado)
            let valcontrapartida = this.limpiarMoneda(this.contrapartida)

            let total = parseFloat(valcofinanciado) + parseFloat(valcontrapartida)
            this.valortotal = total
            return this.number_format(total,2)
        },

        tiempoejecucion: function(){

            if(this.fechasuscripcion != "" && this.fechaterejecucion != ""){
                var fecha1 = new Date(this.fechaterejecucion)
                var fecha2 = new Date(this.fechasuscripcion)

                let diferencia = fecha1.getTime() - fecha2.getTime()
                var dias = diferencia / (1000*60*60*24)

                this.tiempo = dias

                return dias

            } else {

                this.tiempo = 0
                return 0

            }

        }
    },
        
    mounted() {
        
        this.traerDataConvenio()
        this.traerdata(this.convenio)

    },   

}
</script>

